
<template>

  <div class="table">
    <p class="title">
      错题详情
    </p>
    <section class="content-el">
      <div>
      <label style="color: #909399;
    font-weight: 500;">课程名称：</label>
    <el-input  placeholder="请输入课程名称" style="width: 200px;margin-right: 100px;" v-model="subject" clearable>
      </el-input>
      <label style="color: #909399;
    font-weight: 500;" >考试日期：</label>
    <el-date-picker
      type="date"
      placeholder="选择日期" style="margin-right: 100px;" v-model="date" value-format="yyyy-MM-dd">
    </el-date-picker>
    <el-button type="primary" @click="onSubmit" icon="el-icon-search">查询</el-button>
    <el-button type="primary" @click="exportExcel" :loading="loadingExcel" icon="el-icon-download" >导出excel</el-button>
    </div>
      <el-table ref="filterTable" :data="error" v-loading="loading">
        <el-table-column
          prop="examDate"
          label="考试日期"
          sortable
          width="150">
        </el-table-column>
        <el-table-column
          prop="subject"
          label="课程名称"
          width="150"
          filter-placement="bottom-end">
          <template slot-scope="scope">
            <el-tag>{{scope.row.subject}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="title"
          label="题目"
          width="350"
          filter-placement="bottom-end">
          <!-- <template slot-scope="scope">
            <el-tag>{{scope.row.title}}</el-tag>
          </template> -->
        </el-table-column>
        <el-table-column prop="yourAnswer" label="你的答案" width="200">
          <template slot-scope="scope">
            {{scope.row.yourAnswer}}
          </template>
        </el-table-column>
        <el-table-column prop="rightAnswer" label="正确答案" width="200">
          <template slot-scope="scope" >
           <span style="color: red;">{{scope.row.rightAnswer}}</span>
          </template>
        </el-table-column>
      </el-table>
      <el-row type="flex" justify="center" align="middle" class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="pagination.current"
          :page-sizes="[4,6,8,10]"
          :page-size="pagination.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="pagination.total">
        </el-pagination>
      </el-row>
    </section>
  </div>
</template>


<script>
export default {
  data() {
    return {
      loadingExcel:false,
      pagination: { //分页后的留言列表
        current: 1, //当前页
        total: null, //记录条数
        size: 10 //每页条数
      },
      loading: false, //加载标识符
      error: [], //错题
      filter: null, //过滤参数
      date:'',
      subject:''
    }
  },
  created() {
    let errorflag=this.$cookies.get("error")
    if(errorflag==1)
    {

      this.fromTable()
      this.$cookies.remove("error")
    }
    else{
      this.getScore()
    }
    this.loading = true //数据加载则遮罩表格
  },
  methods: {
    exportExcel(){
      let studentId = this.$cookies.get("cid")
      this.loadingExcel=true
      this.$axios({
            headers: {
                'Content-Type': 'application/json'
            },
            url:'/api/export',
            method:'post',
            responseType:'blob',
            data:{
              studentId:studentId,
              subject:this.subject,
              date:this.date
            }
            }).then(res=>{
              this.loadingExcel=false
              let blob = new Blob([res.data], { type: "application/xlsx" });

                let url = window.URL.createObjectURL(blob);
                const link = document.createElement("a"); // 创建a标签
                link.href = url;
                link.download = "错题详情"+".xlsx"; // 重命名文件
                link.click();
                URL.revokeObjectURL(url);

                this.$message({
                  type:'success',
                  message:'导出成功'
                })
          }).catch(()=>{
            this.loadingExcel=false
            this.$message({
                  type:'error',
                  message:'请求超时'
            })
          })
    },
    getScore() {
      let studentId = this.$cookies.get("cid")
      this.$axios(`/api/error/${this.pagination.current}/${this.pagination.size}/${studentId}`).then(res => {
        if(res.data.code == 200) {
          this.loading = false //数据加载完成去掉遮罩
          this.error = res.data.data.records
          this.pagination = {...res.data.data}
          let mapVal = this.error.map((element,index) => { //通过map得到 filter:[{text,value}]形式的数组对象
            let newVal = {}
            newVal.text = element.answerDate
            newVal.value = element.answerDate
            return newVal
          })
          let hash = []
          const newArr = mapVal.reduce((item, next) => { //对新对象进行去重操作
            hash[next.text] ? '' : hash[next.text] = true && item.push(next);
            return item
          }, []);
          this.filter = newArr
        }
      })
    },
    //改变当前记录条数
    handleSizeChange(val) {
      this.pagination.size = val
      this.fromTable()
    },
    //改变当前页码，重新发送请求
    handleCurrentChange(val) {
      this.pagination.current = val
      this.fromTable()
    },
    formatter(row, column) {
      return row.address;
    },
    filterTag(value, row) {
      return row.tag === value;
    },
    filterHandler(value, row, column) {
      const property = column["property"];
      return row[property] === value;
    },
    onSubmit(){
      //发请求然后换数据
      let studentId = this.$cookies.get("cid")
      this.pagination.current=1
      this.$axios({
            headers: {
                'Content-Type': 'application/json'
            },
            url:'/api/error/'+this.pagination.current+'/'+this.pagination.size,
            method:'post',
            data:{
              studentId:studentId,
              subject:this.subject,
              date:this.date
            }
            }).then(res=>{
              this.loading = false //数据加载完成去掉遮罩
          this.error = res.data.data.records
          this.pagination = {...res.data.data}
          let mapVal = this.error.map((element,index) => { //通过map得到 filter:[{text,value}]形式的数组对象
            let newVal = {}
            newVal.text = element.answerDate
            newVal.value = element.answerDate
            return newVal
          })
          let hash = []
          const newArr = mapVal.reduce((item, next) => { //对新对象进行去重操作
            hash[next.text] ? '' : hash[next.text] = true && item.push(next);
            return item
          }, []);
          this.filter = newArr
          })
    },
    fromTable(){
      let studentId = this.$cookies.get("cid")
      if(this.$cookies.get("date")!=null){
        this.date=this.$cookies.get("date")
        this.subject=this.$cookies.get("sub")
      }

      this.$axios({
            headers: {
                'Content-Type': 'application/json'
            },
            url:'/api/error/'+this.pagination.current+'/'+this.pagination.size,
            method:'post',
            data:{
              studentId:studentId,
              subject:this.subject,
              date:this.date
            }
            }).then(res=>{
              this.loading = false //数据加载完成去掉遮罩

              this.$cookies.remove("sub")
              this.$cookies.remove("date")
          this.error = res.data.data.records
          this.pagination = {...res.data.data}
          let mapVal = this.error.map((element,index) => { //通过map得到 filter:[{text,value}]形式的数组对象
            let newVal = {}
            newVal.text = element.answerDate
            newVal.value = element.answerDate
            return newVal
          })
          let hash = []
          const newArr = mapVal.reduce((item, next) => { //对新对象进行去重操作
            hash[next.text] ? '' : hash[next.text] = true && item.push(next);
            return item
          }, []);
          this.filter = newArr
          })
    }
  }
};
</script>

<style lang="less" scoped>

.pagination {
  padding-top: 20px;
}
.table {
  width: 1110px;
  margin: 0 auto;
  .title {
    margin: 20px;
  }
  .content-el {
    background-color: #fff;
    padding: 20px;
  }
}
</style>



